import 'bootstrap/dist/css/bootstrap.min.css';
import {useState , useEffect} from 'react';
import {collection,addDoc,getDocs,getDoc,doc,updateDoc, deleteDoc} from 'firebase/firestore'
import {db} from './firebaseConfig'

function TodoFirebase() {

    
    const todoCollectionRef = collection(db,'todo-collection')
    
    const [task , setTask] = useState([])
    const [input , setInput] = useState('')
    
    async function getTask(){
        const data = await getDocs(todoCollectionRef)
        const finalData = data.docs.map(e=>({...e.data(),id:e.id}));
        setTask(finalData);
    }

    useEffect(()=>{
        getTask()
    },[])

  async function addTask(input){
    const data = {
        title:input,
        status:false
    }
    const added = await addDoc(todoCollectionRef,data);
    // console.log(added)
    setInput('')
    getTask()
  }

 async function changeStatus(id){
    // const docRef = await doc(todoCollectionRef,id);
    const docRef = await doc(db,'todo-collection',id);
    const find = await getDoc(docRef)
    // console.log(find.data());

    await updateDoc(docRef,{...find.data(),status:!find.data().status})
    getTask();
  }

  async function deleteTask(id){
    const docRef = await doc(db,'todo-collection',id);
    await deleteDoc(docRef);
    getTask()
  }


  return (
    <div className="row mt-5">
      <div className='col-6 mx-auto'>
        <div className='form-group d-flex my-3'>
          <input onChange={e => setInput(e.target.value)} value={input} className='form-control' placeholder='write something...' />
          <button onClick={()=>addTask(input)} className='btn btn-danger mx-auto ms-3'>Save</button>
        </div>
        <ul className='list-group'>
          {task.map(item => (
              <li onDoubleClick={() => deleteTask(item.id)} onClick={()=>changeStatus(item.id)} key={item.id} className={`list-group-item ${item.status?`list-group-item-success`:`list-group-item-danger`}`}>{item.title}</li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default TodoFirebase;
